/* ./src/index.css */
/* @tailwind base;
@tailwind components;
@tailwind utilities; */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: white;
  scroll-behavior: smooth;
}

/* homepage css */
.container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  /* position: relative; */
}
/* .container-center {
  align-content: space-around;
} */

.image {
  width: 100%;
  height: 100vh;
}

.homepage-title {
  font-family: "Orelega One", cursive;
  font-size: 40px;
  position: absolute;
  color: darkslategray;
  text-align: center;
  margin-top: 130px;
  /* align-content: space-around; */
}

a,
a:visited {
  text-decoration: none;
}

.homepage-btn {
  position: absolute;
  font-family: "Orelega One", cursive;
  background-color: white;
  font-size: 25px;
  border: darkslategray solid 2px;
  color: darkslategray;
  border-radius: 5px;
  padding: 5px;
  word-break: break-word;
  align-self: flex-end;
  margin-bottom: 75px;
  /*how to get space around title and btn*/
}

.homepage-btn:hover {
  background-color: darkslategray;
  border: white solid 2px;
  color: white;
}

/* lister css */
.nav {
  font-family: "Montserrat", sans-serif;
}

.nav-header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #3c3d40;
  color: white;
  font-size: 25px;
  padding: 15px 15px;
}

.fa-arrow-left {
  color: white;
  display: flex;
  align-items: center;
}

a,
a:visited {
  text-decoration: none;
}

.title {
  text-align: center;
}

.nav-toggle {
  border: none;
  color: white;
  background-color: #3c3d40;
  font-size: 25px;
  display: flex;
  align-items: center;

  /* background-color: white; */
  /*work out how to color the toggle properly*/
}

.back-btn {
  position: absolute;
  left: 10px;
}

.menu-btn {
  position: absolute;
  right: 10px;
}

.fixed-nav {
  position: fixed;
  width: 100%;
  z-index: 100;
  /* transition-duration: 4s; */
}

.filter-container {
  overflow: hidden;
  transform: translate(+120%);
  transition: 2s;
  height: 0;
  background-color: #3c3d40;
  color: white;
}

.show-filter-container {
  overflow: visible;
  height: auto;
  transform: translate(0%);
  position: fixed;
  right: 0%;
  width: 80%;
  z-index: 100;
}

.opaque-background {
  opacity: 0.3;
  z-index: -100;
}

.filter-header {
  font-size: 18px;
  /* font-weight: 800; */
  display: block;
  padding: 10px;
  /* border-top: solid 1px lightgrey; */
  background-color: #3c3d40;
}

.filter-group {
  padding: 10px;
  border-top: solid 1px lightgrey;
  border-bottom: solid 1px lightgrey;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.filter-wrapper,
.filter-wrapper-complete {
  overflow: hidden;
  height: 0;
  transition: 1s;
}

.filter {
  display: flex;
  padding: 10px;
  padding-left: 40px;
  /* border-bottom: solid 1px lightgrey; */
  border-top: solid 1px lightgrey;
  border-bottom: solid 1px lightgrey;
  color: lightgrey;
}

.fa-check {
  padding-right: 10px;
  color: rgb(22, 206, 22);
  visibility: hidden;
  font-size: 22px;
}

.show-fa-check {
  visibility: visible;
  color: rgb(22, 206, 22);
}

.btn-container {
  display: flex;
  justify-content: center;
  padding: 10px;
}

.done-btn {
  width: 90%;
  font-family: "Montserrat", sans-serif;
  color: black;
  font-weight: 800;
  padding: 5px;
  font-size: 14px;
}

.second-nav {
  /*how to make a HTML input field bigger*/
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  background-color: #4d5057;
  font-size: 15px;
  color: white;
  padding: 7px 5px;
  margin-bottom: 10px;
}

.input-field::placeholder,
select {
  text-align: left;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}

.fa-search,
.label {
  padding-right: 2px;
  padding-top: 3px;
  font-size: 17px;
}

.search,
.sort {
  width: 80%;
  padding-top: 5px;
  padding-bottom: 5px;
}

section {
  font-family: "Montserrat", sans-serif;
}

.mountain-border {
  padding: 10px;
  border-radius: 30px;
  border: solid 5px white;
}

.show-mountain-border {
  border: solid 5px rgb(22, 206, 22);
}

.name {
  text-align: center;
  font-weight: 700;
  font-size: 17px;
  padding-bottom: 10px;
}

.mountain-image {
  width: 300px;
  height: 200px;
  display: block;
  margin: auto;
  border-radius: 5px;
}

.image-container {
  display: flex;
  align-items: center;
}

.fa-angle-left,
.fa-angle-right {
  color: rgb(22, 206, 22);
  font-size: 30px;
}

.fa-angle-left {
  margin-right: 5px;
}

.fa-angle-right {
  margin-left: 5px;
}

ul {
  width: 80%;
  font-weight: 600;
  margin: auto;
  color: black;
}

.mountain-region,
.height,
.average-time,
.difficulty,
.complete {
  padding: 10px;
}

.fa-star {
  content: "\f005";
  color: lightgrey;
}

.stars-outer {
  position: relative;
  display: inline-block;
}

.stars-inner {
  position: absolute;
  top: 0;
  left: 0;
  white-space: no-wrap;
  overflow: hidden;
  width: 0;
}

.stars-outer::before {
  content: "\f005 \f005 \f005 \f005 \f005";
  font-family: "Font Awesome 5 Free";
  color: lightgrey;
}

.stars-inner::before {
  content: "\f005 \f005 \f005 \f005 \f005";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  color: gold;
  width: 100%;
}

.form {
  display: flex;
  align-items: center;
}

.checkbox {
  margin-left: 5px;
  margin-top: 2px;
  width: 15px;
  height: 15px;
  background-color: rgb(22, 206, 22);
}

.btn {
  font-family: "Montserrat", sans-serif;
  width: 80%;
  padding: 10px;
  display: block;
  margin: auto;
  margin-top: 10px;
  font-weight: 700;
  font-size: 15px;
  border-radius: 5px;
  text-align: center;
  color: white;
}

.complete {
  background-color: rgb(22, 206, 22);
}

.incomplete,
.more-info {
  background-color: grey;
}

/* .btn:hover {
  transform: scale(1.2);
} */

.seperator {
  width: 90%;
  margin: auto;
}

p {
  color: whitesmoke;
}

footer {
  font-family: "Montserrat", sans-serif;
  text-align: center;
}

/* PDP css */

.pdp-label {
  font-weight: 800;
  display: block;
  text-align: center;
}

.input {
  margin-bottom: 8px;
  width: 150px;
  border: solid 1px black;
}

.input::placeholder,
.input,
.complete {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  border-radius: 5px;
}

.climber,
.view {
  width: 100%;
  padding-top: 25px;
  padding-bottom: 25px;
}

.pdp-container {
  display: flex;
  justify-content: space-between;
}

.total-time {
  display: block;
  margin: 0 auto;
}

.pdp-complete,
.pdp-markasincomplete {
  background-color: rgb(22, 206, 22);
  width: 50%;
  padding: 10px;
  /* padding-top: 5px;
    padding-bottom: 5px; */
  margin: 0 auto;
  margin-top: 10px;
  color: white;
  font-weight: 700;
  font-size: 15px;
  text-align: center;
}

.pdp-markasincomplete {
  background-color: red;
}

